:root {
  --dark-text-color: #404040;
  --mid-text-color: #686868;
  --border-color: #d0d0d0;
  --light-background-color: #f0f0f0;
}

html {
  font-size: 10px;
}

body {
  margin: 0;
  padding: 0;
  font-family: Georgia, serif;
}

header {
  padding: 1em;
  background-color: #f0f0f0;
  border-bottom: 1px solid #d0d0d0;
}

h1 {
  margin: 0;
}

main {
  padding: 16px;
}

button {
  margin: 0 8px;
}

.phraseDisplay {
  font-size: 3.0rem;
}

.phonemeReference {
  margin-top: 16px;
  font-size: 1.4rem;
}
